import React, {  useState } from 'react'
import "../node_modules/bootstrap/dist/css/bootstrap.min.css"
import '../src/App.css'
import uuid from 'uuid'
const App=()=>{
    const [list,setList]=useState([])
  function run(){
   if (document.getElementById('text').value)
    {const item={
      id:uuid.v4(),
      value:document.getElementById('text').value,
      Date: new Date().toUTCString()
    }
    if(localStorage.getItem('task')==null){
      const list=[]
      list.push(item)
      localStorage.setItem('task',JSON.stringify(list))
      setList(JSON.parse(localStorage.getItem('task')))
    }
    else{
      const list=JSON.parse(localStorage.getItem('task'))
      list.push(item)
      localStorage.setItem('task',JSON.stringify(list))
      setList(JSON.parse(localStorage.getItem('task')))
    }
   
  }
    else{
      return false
    }
  }
  window.addEventListener('load',(e)=>{
    const list=JSON.parse(localStorage.getItem('task'))
    setList(list)

})
  
return(
  <>
  <div className='row'>
    <div className='col-12 text-center heading' ><span className='main_heading'>T̶o̶D̶o̶ ̶L̶i̶s̶t̶</span></div>
    <div className='col-12 data' >
  <ul style={{listStyle:'none',height:'400px',overflow:'auto'}}>
  {
    list.map((item,index)=>
    <>
    <div className='row ' >
    <li className='col-6 li'  key={item.id}>{item.value}
    </li>
    <i  className='col-6 fa fa-times' onClick={(e)=>{var index=e.target.getAttribute('data-key');const list=JSON.parse(localStorage.getItem('task'));list.splice(index,1);localStorage.setItem('task',JSON.stringify(list));setList(list)}} data-key={index} aria-hidden="true"></i>
      
    </div>
    </>)

  }
  </ul>
  </div>
  <div className='col-12 ' >
  <input id="text" type='text' className='inpt' />
  <button className='button' onClick={()=>{run()}}>+</button>
  
  </div>
  </div>
  </>
)
}
export default App